/* eslint-disable multiline-ternary */
import Spinner from "@components/spinner/Loading-spinner"
import UILoader from "@components/ui-loader"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import "moment/locale/id"
import { Fragment, useContext, useState } from "react"
import { Controller, useForm } from "react-hook-form"
import toast from "react-hot-toast"
import { useTranslation } from "react-i18next"
import { useParams } from "react-router-dom"
import {
  Button,
  CardText,
  Col,
  Form,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  Row
} from "reactstrap"
import {
  getDataById,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  storeName
} from "../store"

import { useDispatch, useSelector } from "react-redux"

const ModalAddEdit = ({
  show,
  setShow,
  detailPage,
  formState,
  setFormState
}) => {
  const { t } = useTranslation()
  const { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { colors } = useContext(ThemeColors)
  const [tgl1, setTgl1] = useState(null)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
        <CardText className="mb-0 mt-1">{t(loadingText)}...</CardText>
      </Fragment>
    )
  }

  function sortedObject(unordered) {
    return Object.keys(unordered)
      .sort()
      .reduce((obj, key) => {
        obj[key] = unordered[key]
        return obj
      }, {})
  }

  const countChanges = (data) => {
    if (formState === "edit") {
      const storeData = { ...sortedObject(store.selectedData) }
      delete storeData["id"]
      for (let key in storeData) {
        if (!(key in data)) delete storeData[key]
      }
      // console.log(JSON.stringify(sortedObject(storeData)))
      // console.log(JSON.stringify(sortedObject(data)))
      return (
        JSON.stringify(sortedObject(storeData)) !==
        JSON.stringify(sortedObject(data))
      )
    } else {
      return true
    }
  }
  const onChangeTgl1 = (event) => {
    setTgl1(event)
  }

  const updateDataToApi = (data) => {
    if (countChanges(data)) {
      if (
        Object.values(data).every((field) => {
          return field !== null && field !== ""
        })
      ) {
        setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
        dispatch(putData(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setShow(!show)
            toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
            if (detailPage) {
              dispatch(getDataById(id))
            }
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const postDataToApi = (data) => {
    if (
      Object.values(data).every((field) => {
        return field !== null && field !== ""
      })
    ) {
      setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)
      dispatch(postData(data)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setShow(!show)
          toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
          if (detailPage) {
            dispatch(getDataById(id))
          }
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key]?.length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  const handleReset = () => {
    dispatch(resetSelectedData())
  }

  return (
    <Modal
      isOpen={show}
      toggle={() => {
        handleReset()
        setShow(!show)
      }}
      className="modal-dialog-centered modal-lg"
    >
      <UILoader
        blocking={store.isLoadingAddEdit || store.isLoadingDetail}
        loader={<Loader />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => {
            handleReset()
            setShow(!show)
          }}
        ></ModalHeader>
        <ModalBody className="px-sm-5 pt-50 pb-5">
          <div className="text-center mb-2">
            <h2 className="mb-1">
              {formState === "edit" ? t("Edit") : t("Add")} {t(pageTitle)}
            </h2>
          </div>
          <Form
            onSubmit={
              formState === "edit"
                ? handleSubmit(updateDataToApi)
                : handleSubmit(postDataToApi)
            }
          >
            <Row className="gy-1 pt-75">
              <Col md={12} xs={12}>
                <Label className="form-label" for="name">
                  {t("Name")}
                  <span className="text-danger">*</span>
                </Label>
                <Controller
                  defaultValue={store.selectedData?.name ?? ""}
                  control={control}
                  id="name"
                  name="name"
                  render={({ field }) => (
                    <Input
                      {...field}
                      className={
                        errors.name ? "form-control is-invalid" : "form-control"
                      }
                      id="name"
                      invalid={errors.name && true}
                    />
                  )}
                />
              </Col>

              <Col md={12} xs={12}>
                <Label className="form-label" for="description">
                  {t("Description")}
                  <span className="text-danger">*</span>
                </Label>
                <Controller
                  defaultValue={store.selectedData?.description ?? ""}
                  control={control}
                  id="description"
                  name="description"
                  render={({ field }) => (
                    <Input
                      {...field}
                      className={
                        errors.description
                          ? "form-control is-invalid"
                          : "form-control"
                      }
                      id="description"
                      invalid={errors.description && true}
                    />
                  )}
                />
              </Col>

              <Col xs={12} className="text-center mt-2 pt-50">
                <Button type="submit" className="me-1" color="primary">
                  {t("Submit")}
                </Button>
                <Button
                  type="reset"
                  color="secondary"
                  outline
                  onClick={() => {
                    handleReset()
                    setFormState("add")
                    setShow(false)
                  }}
                >
                  {t("Cancel")}
                </Button>
              </Col>
            </Row>
          </Form>
        </ModalBody>
      </UILoader>
    </Modal>
  )
}

export default ModalAddEdit
